.dashboard-header-container {
    height: 75px;
    width: 100%;
}

.logo-container {
    margin-top: -10px;
}

.logo-footer-style {
    color: black;
    font-size: 9px;
    float: right;
}

.demo-button-style {
    margin-top: -5px;
}

.user-header-container .logo-container {
    margin-top: 0px;
}

.user-header-container .logo-container img {
    height: 80px;
}

.dashboard-link {
    color: black;
    font-weight: bold;
}

.dashboard-link:hover {
    text-decoration: none;
}

#header-nav {
    position: fixed; /* Make it stick/fixed */
    top: 0; /* Stay on top */
    width: 100%; /* Full width */
    transition: top 0.3s; /* Transition effect when sliding down (and up) */
}

#header-nav .navbar-nav,
#header-nav .navbar-nav .nav-link {
    font-weight: bold;
    color: #384d60 !important;
}

.header-background {
    position: absolute;
    top: 0;
    left: 0;
    width: 80%;
    height: 100%;
    /* filter: opacity(.7); */
    z-index: -1;
}

#header-nav {
    opacity: .7;
    background-color: #DDE1E4;
}

#header-nav:hover {
    background-color: white;
    opacity: 1;
}
.header-content {
    /* background: none; */
    z-index: 10;
}
